.item {
  font-family: 'reco';
  text-align: center;
  padding-top: 15px;
  padding-bottom: 15px;
  width: 150px;
  color: white;
  text-decoration: none;
  font-size: 1.1rem;
}

.text-overlay {
  position: absolute;
  top: -10px;
  left: 120px;
  color: black;
  background: white;
  padding: 10px;
  font-size: 25px;
  margin-top: 60px;
}
.pic-overlay {
  position: absolute;
  top:-3%;
  left: 7%;
  color: black;
  background: white;
  padding: 0.625rem;
  margin-top: 12%;
}
.P1text {
  position: absolute;
  font-family: 'reco';
  top: 25rem;
  /* 텍스트의 상단 여백 설정 */
  left: -50rem;
  /* 텍스트의 왼쪽 여백 설정 */
  color: black;
  /* 텍스트 색상 설정 */
  background: whitesmoke;
  /* 텍스트 배경색 설정 */
  padding: 0.625rem;
  /* 텍스트 주변 여백 설정 */
  font-size: 1.25rem;
  /* 텍스트 크기 설정 */
  margin-top: 13%;
  width:37.5rem;
}

